const KakaoShareButton = ({
	challengeTitle,
	imageUrl,
	tags,
	social,
	buttons,
	onImgClick,
}) => {
	useEffect(() => {
		createKakaoButton();
		const kakaoImg = document.getElementById('kakao-link-btn');
		kakaoImg.addEventListener('click', () => {
			onImgClick();
		});
	});
	const createKakaoButton = () => {
		// kakao sdk script   window.Kakao  
		if (window.Kakao) {
			const kakao = window.Kakao;
			//  initialization 
			if (!kakao.isInitialized()) {
				//  step   javascript key   initialize
				kakao.init(process.env.REACT_APP_KAKAO_KEY);
			}
			kakao.Link.createDefaultButton({
				// Render  id=kakao-link-btn     
				container: '#kakao-link-btn',
				objectType: 'feed',
				content: {
					title: challengeTitle,
					// description: tags.join(' ') ,
					imageUrl: imageUrl, // i.e. process.env.FETCH_URL + '/logo.png'
					link: {
						mobileWebUrl: window.location.href,
						// mobileWebUrl: 'https://www.naver.com',
						webUrl: window.location.href,
						// webUrl: 'https://www.naver.com',
					},
				},
				// social: social,
				// buttons: buttons,
			});
		}
	};
	return (
		<div
			onClick={() => {
				console.log('clicked');
			}}
		>
			<img
				className="kakao-share-button"
				id="kakao-link-btn"
				src="/img/ic_kakao.svg"
			></img>
		</div>
	);
};
export default KakaoShareButton;
